import React, { memo, useCallback, useEffect, useRef, useState } from "react";
import styled from "styled-components";
import SideBar from "../components/SideBar";
import { useSelector } from "react-redux";
// import { toggleBtn } from '../Slices/MapSlice';
import arrowL from "../asset/img/left.png";
import arrowR from "../asset/img/right.png";

const MapCss = styled.div`
  display: flex;
  flex-direction: row;

  .map {
    width: 100%;
    height: 100vh;
    background-color: gray;
    display: flex;
    flex-direction: row;

    .sideBtn {
      display: block;
      width: 25px;
      height: 75px;
      background-color: #0280e0;
      margin: auto 0;
      justify-self: center;
      border: 1px solid #0280e0;
      border-left: 0;
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      cursor: pointer;
      z-index: 99;

      .left {
        display: block;
        background: url(${arrowL}) no-repeat;
        background-size: 25px 40px;
        background-position: -7px;
        text-indent: -99999px;
      }
      .right {
        display: block;
        background: url(${arrowR}) no-repeat;
        background-size: 25px 40px;
        background-position: -7px;
        text-indent: -99999px;
      }
    }
    .moveMapBtn {
      width: 100px;
      height: 100px;
      background-color: #0280e0;
      z-index: 99;
    }
  }
`;
const { kakao } = window;

const Map = () => {
  const { documents } = useSelector((state) => state.map);

  //   
  const myMap = useRef();

  //  
  const [button, setButton] = useState(true);

  //   boolean  
  const onClick = useCallback((e) => {
    setButton((button) => !button);
  }, []);

  //   side   state
  const [loc, setLoc] = useState();

  //        
  const maps = (location) => {
    setLoc(location);
  };

  useEffect(() => {
    //    ref   
    const container = myMap.current;

    //      ( )
    const dft = new kakao.maps.LatLng(37.5026, 127.0249);

    //    (,   ,  (+) (-))
    const options = {
      center: dft,
      level: 4,
      keyboardShortcuts: true,
      MaxLevel: 10,
    };
    //  
    var map = new kakao.maps.Map(container, options);

    //   
    const zoomControl = new kakao.maps.ZoomControl();

    //     
    map.addControl(zoomControl, kakao.maps.ControlPosition.LEFT);

    //     
    map.setCursor("move");

    //       
    documents &&
      documents.map(({ x, y }) => {
        return new kakao.maps.Marker({
          map: map,
          position: new kakao.maps.LatLng(y, x),
        });
      });

    //       ()
    if (documents) {
      documents.map(({ y, x }) => {
        let bounds = new kakao.maps.LatLngBounds();
        return map.setBounds(bounds.extend(new kakao.maps.LatLng(y, x)));
      });
    }
    //      
    if (loc) {
      let locBounds = new kakao.maps.LatLngBounds();
      let locLatLng = new kakao.maps.LatLng(loc[0], loc[1]);
      locBounds.extend(locLatLng);
      map.setBounds(locBounds);
      map.panTo(locLatLng);
    }

    //      
    if (button === false) map.relayout();
  }, [button, documents, loc]);

  return (
    <div>
      <MapCss>
        <SideBar button={button} maps={maps} />
        <div className="map" ref={myMap}>
          <button className="sideBtn" onClick={onClick}>
            {/*       */}
            {button && button ? (
              <span className="left">left</span>
            ) : (
              <span className="right">right</span>
            )}
          </button>
        </div>
      </MapCss>
    </div>
  );
};

export default memo(Map);
